}

.homepage>* {
  margin-bottom:10px;
}

.auth-img {
  width:400px;
  height:400px;
}

.author-of-the-day {
  text-align: center;
}

.author-of-the-day {
  width: 80%;
  height: 100%;
  padding: 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: rgb(207, 204, 204);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.author-of-the-day .author-link {
  display: flex;  
  flex-direction: column;
  align-items: center;
  text-decoration: none;
}

.author-of-the-day img {
  width: 400px;
  height: 500px;
  margin: 10px 0;
