.container {
  display: flex;
  width: 1100px;
  height: 80vh;
}

.col,
.row {
  display: flex;
  width: 100%;
}

.col {
  flex-direction: column;
}

.row {
  flex-direction: row;
}

.search-shape {
  margin: 200px auto 50px;
  min-width: 50%;
}

.search-shape input {
  padding: 7px 10px;
  border: 1px solid #e0e0e0;
  border-radius: 5px;
  outline: none;
  font-size: 2em;
  width: 100%;
}

.search-result {
  width: 100%;
  display: flex;
}

.search-result .card {
  margin: 5px auto;
  width: 90%;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.15);
}

.search-result .card img {
  display: flex;
  width: 100px;
  height: 100px;
}

.search-result .card .author-name {
  margin: auto 50px auto;
}

@media only screen and (max-width: 1100px) {
  .container {
    width: 100%;
  }
  .search-shape {
    margin-top: 100px;
  }
}

@media only screen and (max-width: 500px) {
  .card {
    width: 100% !important;
  }
  .search-shape {
    margin: 50px 0 10px;
  }
  .search-shape input {
    width: 100%;
    border-radius: 0;
    border-left: 0;
    border-right: 0;
  }
}
